import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import WorkoutGraph from '../../components/workout/graph';
import WorkoutTable from '../../components/workout/table';
import WorkoutCalendar from '../../components/workout/calendar';
import Teams from '../../components/team/teams';
import Gauge from '../../components/gauge';
import CalendarPlan from '../../components/workout/calendarPlan';
import Strava from '../../components/strava';
import Garmin from '../../components/garmin';

const initialState = {
	isLoading: false,
	submenu: 'totals',
	workoutFilter: () => true,
};

@observer
class Workouts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadWorkouts = async (props = this.props) => {
		this.setState({ isLoading: true });
		const { workoutStore, userStore } = props.stores;
		const { currentTeam } = workoutStore;

		// const currentMonth = new Date().getMonth() + 1;
        // const { month = currentMonth, year = util.getYear(), team = currentTeam, dog = {} } = props;

		// const { year } = props;
		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const { month = currentMonth, year = currentYear } = props;

		await Promise.all([
			workoutStore.load({ query: { team: currentTeam, year, month } }),
			this.loadWorkoutSummary(props),
			this.loadWorkoutPlans(props),
			userStore.getNotifications(),
			this.loadStravaActivities(),
			this.loadGarminActivities(),
		]);
		this.setState({ isLoading: false });
	}

	async loadStravaActivities() {
		const { stravaActivityStore } = this.props.stores;
		stravaActivityStore.load({ query: { hideImported: 1 }});
	}

	async loadGarminActivities() {
		const { garminActivityDetailsStore } = this.props.stores;
		garminActivityDetailsStore.load({ query: { hideImported: 1 }});
	}

	loadWorkoutPlans = async (props = this.props) => {
		this.setState({ isLoading: true });
		const { year } = props;

		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const seasonStart = parseInt(year || (currentMonth < 6 ? currentYear - 1 : currentYear), 10);
		const { workoutPlanStore, workoutStore } = props.stores;
		const { currentTeam } = workoutStore;

		workoutPlanStore.updateItem('workoutPlans', []);
		workoutPlanStore.updateItem('workoutPlan', {});
		workoutPlanStore.resetGraphPlanWeekDistanceCurrent();

		if (currentTeam) {
			await workoutPlanStore.load({ query: {
				team: currentTeam,
				seasonYear: seasonStart,
				seasonMonth: 6,
			} });
			const { workoutPlans } = workoutPlanStore;
			const workoutPlanCurrent = workoutPlans[0] || {};
			if (workoutPlanCurrent.id) {
				await workoutPlanStore.load(workoutPlanCurrent.id);
				workoutPlanStore.prepareWeekGraphs({ year });
			}
		}
		this.setState({ isLoading: false });
	}

	// loadDogs = async () => {
	// 	const { dogStore } = this.props.stores;
	// 	const { currentTeam } = dogStore;
	// 	await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
	// }

	// async loadTracks() {
	// 	const { trackStore } = this.props.stores;
	// 	await trackStore.load();
	// }

	async loadWorkoutSummary(props = this.props) {
		const { userStore, workoutStore } = props.stores;

		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const { month = currentMonth, year = currentYear } = props;

		const { currentTeam } = workoutStore;
        await userStore.getWorkoutSummary({ team: currentTeam, year });
		// await userStore.getWorkoutSummaryDay({ month, year, team: currentTeam });
		// await userStore.getWorkoutSummaryDay({ month, year: year - 1, team: currentTeam });
		await userStore.getWorkoutSummaryDay({ daysBack: 500, team: currentTeam });
    }

	chooseSubmenu = (e) => {
		const submenu = e.target.parentElement.dataset.menu || e.target.dataset.menu;
		this.setState({ submenu });
	}

	chooseDay = ({ day, month, year }) => {
		const { dd, mm, yyyy } = this.state;
		if (dd === day && mm === month && yyyy === year) {
			return this.setState({
				workoutFilter: () => true,
				dd: null,
				mm: null,
				yyyy: null,
			});
		}
		this.setState({
			dd: day,
			mm: month,
			yyyy: year,
			workoutFilter: (w) => {
				const date = new Date(w.date);
				const d = date.getDate();
				const m = date.getMonth() + 1;
				const y = date.getFullYear();
				return d === parseInt(day, 10) && m === parseInt(month, 10) && y === parseInt(year, 10);
			},
		});
	}

	componentDidMount() {
		this.loadWorkouts();
		// this.loadDogs();
		// this.loadTracks();
	}

	componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.month !== this.props.month
            || nextProps.year !== this.props.year
            || nextProps.team !== this.props.team) {
            this.loadWorkouts(nextProps);
        }
    }

	render() {
		const { isLoading, submenu } = this.state;
		const { userStore, appState, workoutStore, workoutPlanStore,
			stravaActivityStore, garminActivityDetailsStore } = this.props.stores;
		const { workoutPlan, workoutPlans } = workoutPlanStore;

		const { user } = userStore;
        const { language = 'no' } = user;
        const MONTHS = mu.getMonths(language);

		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();

		const { month = currentMonth, year = currentYear } = this.props;
		const { currentTeam } = workoutStore;

		const { darkmode } = appState;
		const { workouts } = workoutStore;
		const { workoutSummaryWeek = [] } = userStore;

		const { workoutFilter } = this.state;
		const workoutsFiltered = workouts.filter(workoutFilter);

		const planMonth = workoutPlanStore.sumWorkoutPlanMonth(parseInt(year, 10), parseInt(month, 10)) || 0;
		const summaryMonth = userStore.findWorkoutSummaryMonth(parseInt(year, 10), parseInt(month, 10)) || 0;
		let percentComplete;
		if (planMonth) {
			percentComplete = Math.floor(summaryMonth / planMonth * 100);
		}
		const totalCountMonth = userStore.findWorkoutSummaryMonth(parseInt(year, 10), parseInt(month, 10), 'count', true) || 0;
		const totalDistanceKmMonth = userStore.findWorkoutSummaryMonth(parseInt(year, 10), parseInt(month, 10), 'distanceKm', true) || 0;
		const totalDurationMonth = userStore.findWorkoutSummaryMonth(parseInt(year, 10), parseInt(month, 10), 'duration', true) || 0;
		const totalSpeedavgMonth = userStore.findWorkoutSummaryMonth(parseInt(year, 10), parseInt(month, 10), 'speedAvg', true) || 0;
		const totalElevationMonth = userStore.findWorkoutSummaryMonth(parseInt(year, 10), parseInt(month, 10), 'elevation', true) || 0;

		const seasonStart = parseInt(year || (currentMonth < 6 ? currentYear - 1 : currentYear), 10);

		const { stravaActivities } = stravaActivityStore;
		const { garminActivityDetails } = garminActivityDetailsStore;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row mb-4 fixed-top' style={`margin-top: ${SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center border-bottom border-primary subtopmenu'>
						<a href='/workouts/' class='text-primary stretched-link'><nobr><i class='fas fa-chart-line' /> <Text id='workout.index.dashboard'>Oversikt</Text></nobr></a>
					</div>
					<div class='col text-center subtopmenu'>
						<a href='/workouts/list' class='text-secondary stretched-link'><nobr><i class='fas fa-list' /> <Text id='workout.index.list'>Liste</Text></nobr></a>
					</div>
					<div class='col text-center subtopmenu'>
						<a href='/workouts/plan' class='text-secondary stretched-link'><nobr><i class='fas fa-calendar-alt' /> <Text id='workout.index.plan'>Plan</Text></nobr></a>
					</div>
				</div>
				
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
						<Teams stores={this.props.stores} callback={this.loadWorkouts} key={`teams-${currentTeam}`} />
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-2'>
						<a class='btn btn-primary btn-sm float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> <Text id='workout.index.add-workout'>Legg til treningstur</Text>
						</a>
					</div>

					<WorkoutCalendar stores={this.props.stores} isLoading={isLoading}
						year={year}
						month={month}
						chooseDayFunction={this.chooseDay}
						key={`calendar-header-${workoutPlan.id}-${workoutPlans.length}`}
						hideTable={submenu === 'calendar' ? 0 : 1}
						hideSummary={submenu === 'calendar' ? 0 : 1}
						skipLoadSummary={submenu === 'calendar' ? 0 : 1}
					/>

					{submenu === 'totals' && <>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-2'>
							<div class='row'>
								<div class='col-4 text-center'>
									{planMonth >= 0 && (
										<Gauge 
											key={`gauge-${percentComplete}`}
											radius={55}
											percent={percentComplete}
											prefix='%'
											total={`${planMonth} km`}
											completed={`${summaryMonth} km`}
										/>
									)}<br />
									<small class='text-muted'><Text id='workout.index.plan'>Treningsplan</Text></small>
								</div>
								<div class='col-4 text-center'>
									<h5><span class='text-muted font-weight-lighter'><i class='fas fa-road' /></span></h5>
									<h3>{util.format(totalDistanceKmMonth, 0)} <small><small><small>km</small></small></small></h3>
									<small class='text-muted'>
										<Text id='workout.calendar.distance'>Distanse</Text>
									</small>
								</div>
								<div class='col-4 text-center'>
									<h5><span class='text-muted font-weight-lighter'><i class='fas fa-tachometer-alt ml-3' /></span></h5>
									<h3>{util.format(totalSpeedavgMonth, 1)} <small><small><small>km/t</small></small></small></h3>
									<small class='text-muted'>
										<Text id='workout.calendar.avg-speed'>Gj.snittsfart</Text>
									</small>
								</div>
								<div class='col-12 text-center'>
									<span class='text-muted font-weight-lighter'><i class='fas fa-running' /> </span> {totalCountMonth}

									<span class='text-muted font-weight-lighter'><i class='fas fa-clock ml-3' /></span> {util.secToHms(totalDurationMonth)}
								</div>
							</div>
						</div>
					</>}

					{submenu === 'analysis' && <>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
							<WorkoutGraph stores={this.props.stores} isLoading={isLoading} year={year} key={`graph-${workoutPlan.id}-${workoutPlans.length}`} />
						</div>
					</>}

					{submenu === 'table' && <>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
							<WorkoutTable stores={this.props.stores} isLoading={isLoading} year={year} key={`table-${workoutPlan.id}-${workoutPlans.length}`} />
						</div>
					</>}

					{submenu === 'workoutplan' && <>
						<CalendarPlan stores={this.props.stores}
							basehref='/workouts/'
							month={month}
							year={year}
							key={`${workoutPlan.id || seasonStart}-${currentTeam}-${month}-${year}`}
							showNavigation={false}
							showHeader={false}
						/>
					</>}

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3'>
						<div class='row'>
							<div class='col px-1 pt-2'>
								<button
									class={`btn btn-block ${submenu === 'totals' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
									style='text-overflow: ellipsis; overflow: hidden;'
									data-menu='totals'
									onClick={this.chooseSubmenu}
								><i class='fas fa-tachometer-alt' /> <Text id='workouts.totals'>Totaler</Text></button>
							</div>
							<div class='col px-1 pt-2'>
								<button
									class={`btn btn-block ${submenu === 'analysis' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
									style='text-overflow: ellipsis; overflow: hidden;'
									data-menu='analysis'
									onClick={this.chooseSubmenu}
								><i class='fas fa-chart-area' /> <Text id='workouts.analysis'>Analyser</Text></button>
							</div>
							<div class='col px-1 pt-2'>
								<button
									class={`btn btn-block ${submenu === 'table' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
									style='text-overflow: ellipsis; overflow: hidden;'
									data-menu='table'
									onClick={this.chooseSubmenu}
								><i class='fas fa-table' /> <Text id='workouts.table'>Tabell</Text></button>
							</div>
							<div class='col px-1 pt-2'>
								<button
									class={`btn btn-block ${submenu === 'workoutplan' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
									style='text-overflow: ellipsis; overflow: hidden;'
									data-menu='workoutplan'
									onClick={this.chooseSubmenu}
								><i class='fas fa-calendar-alt' /> <Text id='workouts.workoutplan'>Treningsplan</Text></button>
							</div>
							<div class='col px-1 pt-2'>
								<button
									class={`btn btn-block ${submenu === 'calendar' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
									style='text-overflow: ellipsis; overflow: hidden;'
									data-menu='calendar'
									onClick={this.chooseSubmenu}
								><i class='fas fa-calendar-alt' /> <Text id='workouts.workoutplan'>Kalender</Text></button>
							</div>
						</div>
					</div>


				</div>

				<div class='row'>
					{user && user.strava && stravaActivities && stravaActivities.length > 0 && (
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-top border-bottom pt-3 pb-3 my-2'>
							<h5><Text id='workout.index.strava-workouts'>Strava workouts / not imported</Text></h5>
							{stravaActivities && stravaActivities.map(act =>
								<Strava stores={this.props.stores} activity={act} key={`strava-${currentTeam}-${act.id}`} />)}
						</div>
					)}
					{user && user.garmin && garminActivityDetails && garminActivityDetails.length > 0 && (
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-top border-bottom pt-3 pb-3 my-2'>
							<h5><Text id='workout.index.garmin-workouts'>Garmin workouts / not imported</Text></h5>
							{garminActivityDetails && garminActivityDetails.map(act =>
								<Garmin stores={this.props.stores} activity={act} key={`garmin-${currentTeam}-${act.id}`} />)}
						</div>
					)}
				</div>

				<div class='row'>
					{workoutsFiltered && workoutsFiltered.length > 0 && <div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mt-4'>		
							<h5><Text id='workout.index.last-workouts' fields={{ total: workoutsFiltered.length, month: MONTHS[month - 1], year }}>Treningsturer {MONTHS[month - 1]} {year} - {workoutsFiltered.length} stk</Text></h5>
						</div>
						{workoutsFiltered && workoutsFiltered.map(workout => (
							<Workout stores={this.props.stores} workout={workout} />
						))}
					</div>}

					{!workouts || workouts.length === 0 && <div class='col-12 mb-4 text-center pt-5 pb-5'>
						<h1 class='text-muted'><i class='fas fa-running' /></h1>
						<h5 class='text-muted font-weight-lighter'>
							<Text id='workouts.no-workouts' fields={{ month: MONTHS[month - 1], year }}>Ingen treningsturer registrert...</Text>
						</h5>
					</div>}
				</div>



				{1 === 2 && <div class='row'>
					{workoutSummaryWeek && workoutSummaryWeek.length > 0 && <div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5 class='mt-4'><Text id='workout.index.workout-overview'>Treningsoversikt</Text></h5>
						</div>
						{/* {JSON.stringify(workoutSummaryWeek)} */}
						{workoutSummaryWeek && workoutSummaryWeek.reverse().map(summary => (
							<WorkoutSummary stores={this.props.stores} summary={summary} />
						))}
					</div>}
				</div>}
			</div>
		);
	}
}

export default Workouts;
